<template>
  <div>
    <a-input-search placeholder="搜索设计" @search="onSearch" enterButton />
    <a-list itemLayout="vertical" size="large" :pagination="pagination" :dataSource="allData">
      <div slot="footer">
        <b>Vaemc</b>Design
      </div>
      <a-list-item slot="renderItem" slot-scope="item" key="item.id">
        <template slot="actions" v-for="{type, text} in actions">
          <span :key="type">
            <a-icon :type="type" style="margin-right: 8px" />
            {{text}}
          </span>
        </template>
        <img slot="extra" width="220" alt="logo" :src="item.images[0]" />
        <a-list-item-meta :description="item.subtitle">
          <a slot="title" @click="toDetails(item.id)" :href="item.href">{{item.title}}</a>
        </a-list-item-meta>
        {{item.content}}
      </a-list-item>
    </a-list>
  </div>
</template>
<script>


export default {
  data() {
    return {
      allData: null,
      pagination: {
        onChange: page => {
          console.log(page);
        },
        pageSize: 3
      },
      actions: [
        { type: "star-o", text: "156" },
        { type: "like-o", text: "156" },
        { type: "message", text: "2" }
      ]
    };
  },
  mounted() {
    this.axios
      .get("design")
      .then(response => {
        this.allData = response.data;

        //console.info(response);
      })
      .catch(response => {
        console.log(response);
      });
  },
  methods: {
    toDetails(id) {
      this.$router.push({ path: "/design-details/" + id });
    },
    onSearch() {}
  }
};
</script>